*{
    box-sizing: border-box;
}

body{
    margin:0;

}
.nav{
    background-color: skyblue;
    color:whitesmoke;
    display:flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
    padding: 0 1rem;
}

.nav ul{
    padding: 0;
    margin:0;
    list-style: none;
    display: flex;
    gap:1rem;
}

.nav a {
    color: inherit;
    text-decoration: none; 
    height: 100%;
    display: flex;
    align-items: center;
    padding: .25rem;
}
.nav li.active{
    background-color: aquamarine;
}
.nav li:hover{
    background-color: azure;
}
